<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right mt-4" style="text-align:right">
            <!-- <button class="btn btn-primary" data-toggle="modal" data-target="#productModal">建立新的產品</button> -->
            <button class="btn btn-primary" @click="openModel(true)">建立新的產品</button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th width="120">分類</th>
                    <th>產品名稱</th>
                    <th width="120">原價</th>
                    <th width="120">售價</th>
                    <th width="100">是否啟用</th>
                    <th width="120">編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, key)  in products" :key="item.id">
                    <td>{{ item.category }}</td>
                    <td>{{ item.title }}</td>
                    <td class="text-right">{{ item.origin_price | currency }}</td>
                    <td class="text-right">{{ item.price | currency }}</td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success">啟用</span>
                        <span v-else>未啟用</span>
                    </td>
                    <td>
                        <div class="btn-group">
                            <button class="btn btn-outline-primary btn-sm"
                                @click="openModel(false, item)">編輯</button>
                            <button class="btn btn-outline-danger btn-sm"
                                @click="openDeleteModel(item)"
                            >刪除</button>
                        </div>

                        <!-- <button class="btn btn-outline-primary btn-sm" @click="openModel(false, item)">編輯</button>                    
                        <button class="btn btn-outline-danger btn-sm" @click="openDeleteModel(item)">刪除</button> -->
                    </td>
                </tr>
            </tbody>
        </table>

         <!-- 將 pagination 傳進元件，並且呼叫此元件的事件名稱 changePage -->
        <Pagination :pagination="pagination" @emitPages="getProducts"></Pagination> 
        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
        	<div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>新增產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                        <label for="image">輸入圖片網址</label>
                        <input type="text" class="form-control" id="image"
                            v-model="tempProduct.imageUrl"
                            placeholder="請輸入圖片連結">
                        </div>
                        <div class="form-group">
                        <label for="customFile">或 上傳圖片
                            <i class="fas fa-spinner fa-spin" v-if=status.fileUploading></i>
                        </label>
                        <input type="file" id="customFile" class="form-control"
                            ref="files" @change="uploadFile">
                        </div>
                        <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                        class="img-fluid" :src="tempProduct.imageUrl" alt="">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                        <label for="title">標題</label>
                        <input type="text" class="form-control" id="title"
                            v-model="tempProduct.title"
                            placeholder="請輸入標題">
                        </div>

                        <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category">分類</label>
                                <input type="text" class="form-control" id="category"
                                v-model="tempProduct.category"
                                placeholder="請輸入分類">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">單位</label>
                                <input type="unit" class="form-control" id="unit"
                                v-model="tempProduct.unit"
                                placeholder="請輸入單位">
                            </div>
                        </div>

                        <div class="form-row">
                            <div class="form-group col-md-6">
                            <label for="origin_price">原價</label>
                                <input type="number" class="form-control" id="origin_price"
                                v-model="tempProduct.origin_price"
                                placeholder="請輸入原價">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">售價</label>
                                <input type="number" class="form-control" id="price"
                                v-model="tempProduct.price"
                                placeholder="請輸入售價">
                            </div>
                        </div>
                        <hr>

                        <div class="form-group">
                        <label for="description">產品描述</label>
                        <textarea type="text" class="form-control" id="description"
                            v-model="tempProduct.description"
                            placeholder="請輸入產品描述"></textarea>
                        </div>
                        <div class="form-group">
                        <label for="content">說明內容</label>
                        <textarea type="text" class="form-control" id="content"
                            v-model="tempProduct.content"
                            placeholder="請輸入產品說明內容"></textarea>
                        </div>
                        <div class="form-group">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox"
                                v-model="tempProduct.is_enabled"
                                :true-value="1"
                                :false-value="0"
                                id="is_enabled">
                                <label class="form-check-label" for="is_enabled">
                                是否啟用
                                </label>
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
                    </div>
                </div>
            </div>
		</div>			
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger" @click="deleteProduct()"
                    >確認刪除</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
/* global $ */
import $ from 'jquery'
import Pagination from "@/components/Pagination";

export default {
    data(){
        return {
            products: [],
            pagination:{},
            tempProduct:{},
            isNew: false,
            isLoading: false,
            status:{
                fileUploading:false,
            },
        }
    },
    // 使用元件
    components:{
        Pagination
    },
    methods:{
        getProducts(page = 1){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/products?page=${page}`
            const vm = this;
            console.log(process.env.APIPATH, process.env.CUSTOMERPATH);
            vm.isLoading = true;
            this.$http.get(api).then((resopnse)=>{
                console.log(resopnse.data);
                vm.isLoading = false;
                vm.products = resopnse.data.products;
                vm.pagination = resopnse.data.pagination;
            })
        },
        openModel(isNew, item){
            if(isNew){
                this.tempProduct = {};
                this.isNew = true;                
            }else {
                this.tempProduct = Object.assign({}, item); // ES6 寫法
                                                            // 將item 寫到一個空的集合裡面，
                                                            // 且可以避免 tempProduct 與 item 互相參考
                                                            // https://courses.hexschool.com/courses/vue/lectures/11949339
                this.isNew = false;    
            }
            $('#productModal').modal('show');
        },
        updateProduct(){
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/product`
            let httpMethod = 'post';
            const vm = this;
            if(!vm.isNew){
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/product/${vm.tempProduct.id}`
                httpMethod = 'put';
            }
            
            console.log(process.env.APIPATH, process.env.CUSTOMERPATH);
            this.$http[httpMethod](api, {data:vm.tempProduct}).then((resopnse)=>{
                console.log(resopnse.data);
                if(resopnse.data.success){
                    $('#productModal').modal('hide');
                    vm.getProducts();
                } else{
                    $('#productModal').modal('hide');
                    vm.getProducts();
                    console.log('新增失敗')
                }
                // vm.products = resopnse.data.products;
            })
        },
        openDeleteModel(item){
            this.tempProduct = Object.assign({}, item); // ES6 寫法
                                                        // 將item 寫到一個空的集合裡面，
                                                        // 且可以避免 tempProduct 與 item 互相參考
                                                        // https://courses.hexschool.com/courses/vue/lectures/11949339
            $('#delProductModal').modal('show');
        },
        deleteProduct(){
            var vm = this;
            var api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/product/${vm.tempProduct.id}`
            var httpMethod = 'delete';
            this.$http.delete(api).then((resopnse)=>{
                console.log(resopnse.data);
                if(resopnse.data.success){
                    $('#delProductModal').modal('hide');
                    vm.getProducts();
                } else{
                    $('#delProductModal').modal('hide');
                    vm.getProducts();
                    console.log('刪除失敗')
                }
            })
        },
        uploadFile(){
            console.log(this);
            const uploadedFile = this.$refs.files.files[0];
            const vm = this;
            // FormData 說明文件
            // https://developer.mozilla.org/en-US/docs/web/api/formdata/formdata
            // 上傳格式要求
            // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D
            const formData = new FormData();
            formData.append('file-to-upload', uploadedFile);
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/upload`;
            vm.status.fileUploading = true;
            this.$http.post(url, formData,{
                headers:{
                    'Content-Type' : 'multipart/form-data'
                }
            }).then((response)=>{
                console.log(response.data);
                vm.status.fileUploading = false;
                if(response.data.success){
                    console.log(response.data);
                    vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
                } else{
                    this.$bus.$emit('message:push', response.data.message, 'danger');
                }
            })
        },
    },
    created() {
        this.getProducts();
    }
    
}
</script>


